import logo from './logo.svg';
import './App.css';
import { Row, Col, Container, Modal, Button } from 'react-bootstrap';
import { useState, useEffect } from 'react';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';

function App() {

  const [showInputTag, setShowInputTag] = useState({
    show: false,
    target: ''
  });

  const [ fileName, setFileName ] = useState('');
  const [ fileSrc, setFileSrc ] = useState('');
  const [ fileObject, setFileObject ] = useState();
  const [ cropped, setCropped ] = useState({
    unit: '%',
    width: 100,
    height: 100,
  })
  const [ croppedImage, setCroppedImage ] = useState({
    imageRef: '',
    blobObject: [],
  })

  const [showModal, setShowModal] = useState(false);
  const handleClose = () => setShowModal(false);

  useEffect(() => {
    if(fileName !== ''){
      const render = new FileReader();
      render.addEventListener('load', () =>{
          setShowModal(true);
          setFileSrc(render.result);
      });
      render.readAsDataURL(fileObject);
    }
  }, [fileObject])

  const fileOnClick = (e) => {
    setFileName(e.target.files[0].name);
    setFileObject(e.target.files[0])
  }

  const onImageLoaded = (image) => {
    croppedImage.imageRef = image;
  };

  const onCropComplete = (crop) => {
      makeClientCrop(crop);
  };

  const onCropChange = (crop, percentCrop) => {
    setCropped(crop);
  };

  const makeClientCrop = async (crop) => {
      if (croppedImage.imageRef && crop.width && crop.height) {
          const croppedImageUrl = await getCroppedImg(
          croppedImage.imageRef,
          crop,
          fileName
        );
      }
  }

  const getCroppedImg = (image, crop, fileName) => {
      const canvas = document.createElement('canvas');
      const scaleX = image.naturalWidth / image.width;
      const scaleY = image.naturalHeight / image.height;
      canvas.width = (crop.width + 200 < 500)? crop.width + 200 : 500;
      canvas.height = (crop.height + 200 < 500)? crop.height + 200 : 500;
      const ctx = canvas.getContext('2d');
      
      ctx.drawImage(
        image,
        crop.x * scaleX,
        crop.y * scaleY,
        crop.width * scaleX,
        crop.height * scaleY,
        0,
        0,
        canvas.width,
        canvas.height
      );
          
      return new Promise((resolve, reject) => {
        canvas.toBlob(blob => {
          if (!blob) {
            return;
          }
          croppedImage.blobObject = blob;
        }, 'image/jpeg',1);
      });
      
    }
  
  const getCroppedImage = () => {
    var element = document.createElement("a");
    element.href = window.URL.createObjectURL(croppedImage.blobObject);
    element.download = fileName;
    element.click();
    setShowModal(false);
  }

  const cropButton = () => {
    setShowInputTag({show: true, target: 'crop'});
  }

  const compressButton = () => {
    setShowInputTag({show: true, target: 'compress'});
  }

  return (
    <div>
      <h2 className='text-center'>CHANGE IMAGE</h2>
      <h3 className='head-text'>Select the option to get image you want</h3>
      <div className='margin'></div>
      {/* <Row>
        <Col> */}
          <p 
            className={(showInputTag.target == 'crop')? 'cropButton selected' : 'cropButton'}
            onClick={()=>cropButton()}
          >
            Crop Or Compress
          </p>
        {/* </Col> */}
        {/* <Col>
          <p 
            className={(showInputTag.target == 'compress')? 'compressButton selected' : 'compressButton'}
            onClick={()=>compressButton()}
          >
            Compress
          </p>
        </Col> */}
      {/* </Row> */}
      <Container>
      {/* {(showInputTag.show === true) ? 
        <h5 className='note'>*Select The Image That you want to {showInputTag.target}*</h5>
        : ''
      } */}
      {(showInputTag.show === true) ? 
        <input 
          type='file' 
          className="form-control" 
          placeholder={showInputTag.target} 
          id="inputGroupFile01"
          aria-describedby="inputGroupFileAddon01"
          onChange = { (e) => fileOnClick(e)}/>
        : ''
      }
      </Container>
      <Modal
        show={showModal}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        
        <Modal.Body>
        <Container>
          <Row>
          <Col xs={1} md={3} lg={3}></Col>
            <Col xs={10} md={6} lg={6}>
              {(fileSrc) && ( 
                <ReactCrop 
                  src={fileSrc} 
                  crop={cropped} 
                  ruleOfThirds
                  onImageLoaded={e => { onImageLoaded(e) } }
                  onComplete={e => { onCropComplete(e) } }
                  onChange={e => { onCropChange(e) }}
                />
                )}
            </Col>
            <Col xs={1} md={3} lg={3}></Col>
          </Row>
        </Container>
        </Modal.Body>
        <Modal.Footer>
          <h6 className='note'>For Compress Download image directly without crop</h6>
          <Button variant="secondary" onClick={ () => 
                  getCroppedImage() }>
            Download
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default App;
